import Toast from '../../../../wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			pwdType1: 'password',
			pwdType2: 'password',
			isPassword1: true,
			isPassword2: true,
			oldPwd: '',
			newPwd: '',
			teacherPwd:''
		}
	},
	components: {

	},

	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 切换input框类型
	lookPwd(val) {
		// console.log(val)
		if (val == 1) {
			this.isPassword1 = !this.isPassword1
			if (this.pwdType1 == 'password') {
				this.pwdType1 = 'text'
			} else if (this.pwdType1 == 'text') {
				this.pwdType1 = 'password'
			}
		}
		if (val == 2) {
			this.isPassword2 = !this.isPassword2
			// console.log(this.isPassword2, '111’')
			if (this.pwdType2 == 'password') {
				this.pwdType2 = 'text'
			} else if (this.pwdType2 == 'text') {
				this.pwdType2 = 'password'
			}
		}
	},
		// 获取用户信息
		getUserInfo() {
			this.$minApi.getUserInfo({}).then(data => {
				this.teacherPwd = data.data.teacherPwd
				console.log(data.data.teacherPwd)
			})
		},
		// 修改密码
		changePwd() {
			// console.log(this.teacherPwd)
			// console.log(this.oldPwd)
			if (this.teacherPwd != this.oldPwd) {
				Toast('旧密码不正确')
				return
			}
			var reg = /(?!^\d+$)(?!^[A-Za-z]+$)(?!^[^A-Za-z0-9]+$)(?!^.*[\u4E00-\u9FA5].*$)^\S{8,12}$/;
			if (!reg.test(this.newPwd)) {
				Toast('新密码格式不正确！')
				return
			} else {
				Toast('密码正确！')
			}
		}
	},
	created() {
		this.getUserInfo()
	}
}
